}

.cursive {
  font-family: professor, sans-serif;
  font-weight: 100;
  position: relative;
  bottom: 1px;
  text-transform: none;
}

.color-gray > div {
  position: relative;
  z-index: 1;
}
.color-gray > div::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100%;
  left: 50%;
  margin-left: -50vw;
  background: #EDF4F5;
  z-index: -1;
}

.color-white > div {
  position: relative;
  z-index: 1;
}
.color-white > div::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100%;
  left: 50%;
  margin-left: -50vw;
  background: #fff;
  z-index: -1;
}

.space-above-small > div {
  padding-top: 60px;
}
@media screen and (max-width: 700px) {
  .space-above-small > div {
