.field--type-text-long:not(.field--name-field-embed-code) ul {
  list-style: none;
  line-height: 1.4em;
  padding: 0;
}
.field--type-text-long:not(.field--name-field-embed-code) ul li {
  padding-left: 20px;
  position: relative;
  margin-bottom: 8px;
}
.field--type-text-long:not(.field--name-field-embed-code) ul li::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 8px;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background: #69F3D9;
}
.field--type-text-long:not(.field--name-field-embed-code) ol li {
  margin-bottom: 8px;
}
