  padding-bottom: 0;
}
.field--type-text-long:not(.field--name-field-embed-code) a:not(.button) {
  text-decoration: underline;
  transition: background-size 0.5s ease-out;
  background: linear-gradient(#69F3D9, #69F3D9) no-repeat left 123%/0 50%;
  display: inline;
}
