}
.field--type-text-long:not(.field--name-field-embed-code) a:not(.button):hover {
  text-decoration: none;
  background-size: 100% 50% !important;
}
.field--type-text-long:not(.field--name-field-embed-code) a.button {
  position: relative;
  font-size: 16px;
  line-height: 22px;
  display: inline-block;
  padding: 9px 20px 8px;
  color: #0D2D40;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
  z-index: 0;
}
@media screen and (max-width: 850px) and (min-width: 650px) {
  .field--type-text-long:not(.field--name-field-embed-code) a.button {
    font-size: 14px;
    line-height: 20px;
