}
.field--type-text-long:not(.field--name-field-embed-code) a.button:hover {
  text-decoration: none;
}
.field--type-text-long:not(.field--name-field-embed-code) a.button:hover::before {
  transform: scale(1.08);
}
.field--type-text-long:not(.field--name-field-embed-code) a.button::before {
  content: "";
  display: block;
