nav.tabs {
  margin: 30px 0;
}

.ajax-progress,
.ajax-progress:before {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}

.ajax-progress {
  margin-left: -3em;
  margin-top: -100px;
  background-image: none;
  background-color: transparent;
  font-size: 10px;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(105, 243, 217, 0.5);
  border-right: 1.1em solid rgba(105, 243, 217, 0.5);
  border-bottom: 1.1em solid rgba(105, 243, 217, 0.5);
  border-left: 1.1em solid #69F3D9;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: throbber-rotation 1.1s infinite linear;
  animation: throbber-rotation 1.1s infinite linear;
}

@-webkit-keyframes throbber-rotation {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes throbber-rotation {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
